<template>
    <div class="todo-item">
        <p style="padding-bottom: 15px;">
            
            <input type="checkbox" style="float: left;" v-model="this.todo.completed" v-on:change='markComplete'>
            <span style="float: left; text-align: left; width: 85%" v-bind:class="{'is-completed':todo.completed}">{{todo.title}}</span>
            <button @click="$emit('del-todo', todo.id)" class="del">X</button>
        </p>
    </div>
</template>

<script>
export default {
    name: 'TodoItem',
    props: ['todo'],
    methods: {
        markComplete() {
            this.todo.completed = !this.todo.completed;
        }
    }
    
}
</script>

<style scoped>
  .todo-item {
      background: #f4f4f4;
      padding: 1px 10px 10px 1px;
      border-bottom: 1px #cccccc dotted;
  }

  .is-completed {
      text-decoration: line-through;
  }

  .del {
      background: #ff0000;
      color: #fff;
      border: none;
      padding: 5px 9px;
      border-radius: 50%;
      cursor: pointer;
      float: right;
  }
</style>